async function getApiKey(accessToken,apiKey){
  var query = {apiKey:apiKey,access_token:accessToken}
  _Util.loginWithGoogle(GRAPHQLURL,query)
  .then(res => {
    if(res && res.data && res.data.token){
      localStorage.setItem('jwt', res.data.token);  
      window.location.reload(true);
    }     
  }).catch(error => {      
            console.log(error); //eslint-disable-line
  });

}



async function getApiKey2(accessToken){
    let dbNameFb = "firebaseLocalStorageDb";
    let ObjectStoreFb = "firebaseLocalStorage";
    var request = indexedDB.open(dbNameFb, 2);
    request.onsuccess = function(event) {
      let db = request.result;
      //var objStore = db.createObjectStore("user", { active : true });   
      var transaction = db.transaction([ObjectStoreFb], "readwrite");
      var objectStore = transaction.objectStore(ObjectStoreFb);      
      request = objectStore.openCursor();
      request.onerror = function(event) {};
      var _value = {};
      request.onsuccess = function(event) {
        let cursor = event.target.result;
        if (cursor) {  
            _value = cursor.value?cursor.value.value?cursor.value.value:{}:{}; 
            var query = {apiKey:_value.apiKey,access_token:accessToken}
            _value.apiKey && _Util.loginWithGoogle(GRAPHQLURL,query)
            .then(res => {
              if(res && res.data && res.data.token){
                localStorage.setItem('jwt', res.data.token);  
                window.location.reload(true)
              }     
            }).catch(error => {      
              console.log(error); //eslint-disable-line
            });   
            cursor.continue();
        }
        else {
            // no more results
        }       
      };  
      

    request.onerror = function(event) {
      // Manejar errores.
    };

    }
  

}





