export function getFireBaseStorage(param){    
  //return new Promise((resolve,reject)=>{  })
    const state = _State().common;
    var _ImagesList =  state.blobImagesList;
    if(param && !_ImagesList[param]){  

      var mountainImagesRef = _storageRef.child(param);
      mountainImagesRef.getDownloadURL()
      .then(function(url) {    
        _ImagesList[param] = url;
        _Dispatch(UpdKeyValue({key:'blobImagesList',value:_ImagesList}));
        _Dispatch(UpdKeyValue({key:'blobImageObserve',value:state.blobImageObserve+1}));
        //resolve(url);
      })
      .catch(function(error) {
        console.log(error)
        //reject(error)
      });
      return 'https://cockfight.s3.us-east-2.amazonaws.com/galloGray.png';
    }else if(param && _ImagesList[param]){ 
      //resolve(_ImagesList[param]);
      return _ImagesList[param];
    }else{
      return 'https://cockfight.s3.us-east-2.amazonaws.com/galloGray.png';
      //reject({error:'no parametre present'})
    }
  
}



export function getBlobImage(param){
  const state = _State().common;
  var _ImagesList =  state.blobImagesList;
  var lazyImg = `/lazyLoad.svg`;
  if(param && _ImagesList[param]){ 
    return _ImagesList[param];
  }
  else if(param && !_ImagesList[param]){
    return lazyImg;
  }
  else{
    return lazyImg;
  }
}







export function Image2Caching(url,param) {
  return function (dispatch, getState) {   
    const state = getState().common;
    var _ImagesList =  state.blobImagesList;
    if(param && !_ImagesList[param]){    
      _ImagesList[param] = null;
      var xhr = new XMLHttpRequest();
        xhr.open( "GET", url, true );
        xhr.responseType = "arraybuffer";
        xhr.onload = function( e ) {
          if (xhr.status === 200) {
            var arrayBufferView = new Uint8Array( this.response );              
            if(arrayBufferView.length>200){
              //var base64String = btoa(new Uint8Array(arrayBufferView).reduce((data, byte) => data + String.fromCharCode(byte), ''));
              var blob = new Blob( [ arrayBufferView ], { type: "image/jpeg" } );
              var urlCreator = window.URL || window.webkitURL;
              var imageUrl = urlCreator.createObjectURL(blob);              
              //_ImagesList[param].buffer = base64String;
              _ImagesList[param] = imageUrl;   
              dispatch(UpdKeyValue({key:'blobImagesList',value:_ImagesList}));                                  
            } 
          }             
        };  
      xhr.send();    
    }      
  }  
}
















