export function updateHyperMode(doc) {
  return function (dispatch, getState) {    
    const query= ` 
    mutation($doc: UpdateGrabber!){
      payload:  updateHyperMode(flt: $doc)
    }  
    `;
    let variables={doc};
    _Util.fetchGraphQL(GRAPHQLURL,{query,variables}) 
    .then(res => {
      var hyperMode = false;
      if(res && res.data && res.data.payload){
        hyperMode = res.data.payload
      }
      const _state = getState().common; 
      var us_ = _state.activeUserList;
      if(us_){    
        var s = us_[doc.email];   
        const dd = JSON.stringify(s);
        var kt =JSON.parse(dd);          
        kt['hyperMode']=hyperMode;
        us_[doc.email] = kt;
        dispatch(UpdKeyValue({key:'activeUserList',value:us_}));
        dispatch(_formOberves());          
      }     
    }).catch(error => {      
      console.log(error); //eslint-disable-line
    });
  }
}







