function mapStateToProps(state, ownProps) {
  return {       
    user:state.common.user,
    activeUserList:state.common.activeUserList,
    isInProxy:state.common.isInProxy,
    isValidToken: state.common.isValidToken,
    liteMode: state.common.liteMode,
    screen_size: state.common.screen_size,
    path: state.common.path,
  };
}
function mapDispatchToProps(dispatch) {
  return {     
    commonActions: bindActionCreators(commonActions, dispatch),
    dialogActions: bindActionCreators(dialogActions, dispatch)
  };
}

export default connect(mapStateToProps,mapDispatchToProps)(Header);







