class ModalTime extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      drowndropInputText : ``,
      removeOption:false,
      valid:false,
      maxDays:31,
      hh:null,
      min:null
    };
  }
  componentDidMount() {  
    var _th6 = this;
    const {valueField } = _th6.props;
    
    this.checkvalid = this.checkvalid.bind(this);    
    
    if(valueField){
      var t = date2pretyfy(valueField);     
      _th6.setState({hh:t.getHours()})
      _th6.setState({min:t.getMinutes()}) 
      _th6.setState({valid:true}) 
    }    
    
  }  

 

  checkvalid= (h,m) => {   
    
    //const validDateDDMMYYYY = /^(((0[1-9]|[12]\d|3[01])\/(0[13578]|1[02])\/((19|[2-9]\d)\d{2}))|((0[1-9]|[12]\d|30)\/(0[13456789]|1[012])\/((19|[2-9]\d)\d{2}))|((0[1-9]|1\d|2[0-8])\/02\/((19|[2-9]\d)\d{2}))|(29\/02\/((1[6-9]|[2-9]\d)(0[48]|[2468][048]|[13579][26])|((16|[2468][048]|[3579][26])00))))$/g
    if(h<=23 && h>=0 && m<=59 && m>=0){ 
      this.setState({valid:true});
    }else{
      this.setState({valid:false});
    }
    
}

  UpdHour = (r) => {    
    r = parseInt(r);    
    if(r<=23 && r>=0){ 
      this.setState({hh:r});
      //this.checkvalid(r,this.state.mm,this.state.dd)       
    }else{
      this.setState({hh:null});
     // this.checkvalid(r,this.state.mm,this.state.dd)
     }
  }

  UpdMinute = (r) => { 
    r = parseInt(r);
    if(r<=59 && r>=0){     
      this.setState({min:r});
      //this.checkvalid(this.state.min,r,this.state.hh)   
    }else{
      this.setState({min:null});
      //this.checkvalid(this.state.min,r,this.state.hh)    
    }
  }



C_cancel = (r) => {
  if(typeof this.props._close === "function"){
    this.props._close();
  }  
}

SaveC = (r) => {
  if(typeof this.props.valueUpdate === "function"){
    const { forms, form,field } = this.props;
    var dt = forms[form]?forms[form][field]?forms[form][field]:'date':'date'; 
    var nwTm = updateTime(dt,this.state.hh,this.state.min);
    this.props.valueUpdate(nwTm.getTime());
  }
}






  ref = r => {
    this.MS_Elem = r
  }
  updateState(e){    
    this.setState({[e.key]:e.value});
  }
  
 
  render() {    
    const { placeholder,dob,color } = this.props;
    var StyText = {color:color}
      
    var savebutton =  <div className="button disable"><div className="cc" >save </div></div>
    var _dob = ``;
    if(this.state.valid){
      const { forms, form,field } = this.props;
      var dt = forms[form]?forms[form][field]?forms[form][field]:'date':'date'; 
      var nwTm = updateTime(dt,this.state.hh,this.state.min);
      _dob = time2pretyfy((new Date(nwTm)).getTime());
      savebutton =  <div className="button" onClick={this.SaveC.bind(this)}  style={StyText}><div className="cc" >save </div></div>
    }else{      
      _dob = time2pretyfy((new Date()).getTime());
    }
    return(
      <div className="_dateModal_Wrapper">
        <div className="headerDateModal" style={{backgroundColor:color}}>
          <div className="Title">
            {placeholder}
            <div className="date">
            {_dob}
            </div>
          </div>      
        </div>
        <div className="bodyDateModal">
          <div className="formCont">
            <MDateInput color={color} label={`hour`} _maxLengt={2} _text={this.state.hh} _minValue={0}  _maxValue={23} OnChange={this.UpdHour.bind(this)}/>
            <MDateInput color={color} label={`minute`} _maxLengt={2}  _text={this.state.min} _minValue={0}  _maxValue={59} OnChange={this.UpdMinute.bind(this)}/> 
          </div>
        </div>
        <div className="actionContainer">
        <div className="button" onClick={this.C_cancel} style={StyText}>
          <div className="cc" >
            cancel 
          </div>
        </div>
        {savebutton}
        </div>
      </div>
      )  
  }
}








class MDateInput extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      focus : false,
      text:'',
      error:false,
      errMsg:''
    };
  }
  OnChange(e){  
    var _th6 = this;
    const {props} = _th6;  
    if (typeof props.OnChange === 'function') { 
      props.OnChange(e);
    } 
  }

  componentWillMount() {    
  } 

  updState(e){    
    this.setState({[e.key]:e.value});
  }
  
  handleFocus = e => { 
    this.setState({focus:true}); 
  }

  handleBlur = e => { 
    this.setState({focus:false}); 
  }

  handleChange = e => {   
    const { _minValue , _maxValue ,minValueError, maxValueError} = this.props;    
    var value = parseInt(e.target.value);    
    if(parseInt(_maxValue)<value){
      this.setState({error:true,errMsg:maxValueError,text:e.target.value});
      if (typeof this.props.OnChange === 'function') { 
        this.props.OnChange(null);       
      } 
    }else if(parseInt(_minValue)>value){      
      this.setState({error:true,errMsg:minValueError,text:e.target.value});
      if (typeof this.props.OnChange === 'function') { 
        this.props.OnChange(null);       
      }
    }
    else{      
      this.setState({error:false,text:e.target.value});      
      if (typeof this.props.OnChange === 'function') { 
        this.props.OnChange(value);       
      }      
    }  
  };



  render() {  
    var _th6 = this;
    const { label,_maxLengt,color, _text} = _th6.props;
    var _TexT = this.state.text || _text || '';
    var labelStyle = {}
    var _colorS =  color || `#64b5f6`
    var borderBottomStyle = {}
    var borderBottom = `borderBottomActive`
    if(this.state.focus){
      labelStyle = {color:_colorS}
      borderBottom = `borderBottomActive load`
      borderBottomStyle = {backgroundColor:_colorS}
    }
    return(   
        <div className="inputDatePick">
            <div className="base">  
              <div className="date">
                <div className="inputCont">  
                  <input type="number" tabIndex="0" aria-label="Año" maxLength={_maxLengt} value={_TexT} onFocus={this.handleFocus} onChange={this.handleChange} onBlur={this.handleBlur}/>
                  <div className="label" style={labelStyle}>{label}</div>
                </div>
                <div className="borderBottom"/>
                <div className={borderBottom} style={borderBottomStyle}/>
              </div>   
            </div>     
        </div>
    )   
  }
}


function mapStateToProps(state, ownProps) {
  return {
    forms: state.common.forms,
    formObserve: state.common.formObserve
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(commonActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(ModalTime);


function date2pretyfy(dt) {  
  var date = dt?!isNaN(dt)?new Date(parseInt(dt.toString())):new Date():new Date();   
  return date;  
}


function updateTime(dt,hh,min) {
  var y = new Date(dt);
  var result = isNaN(y.getTime())?new Date():y;  
  result.setHours(hh);
  result.setMinutes(min);
  return result;
}

function time2pretyfy(dt) {  
  var date = dt?!isNaN(dt)?new Date(parseInt(dt.toString())):new Date():new Date();   
  var MM = date.getMinutes();
  return `${date.getHours()}: ${MM>9?MM:`0${MM}`}`;  
}