
import React, { Component } from 'react';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as commonActions from '../../state/commonActions';
import * as Util from '../../state/Util';

import './style.css';
import { parse } from 'url';

 /* */


class InputText extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      drowndropInputText : ``,
      text:'',
      valid:true,
      msg:'',
      removeOption:false
    };
  }
  componentWillMount() {    
    const {form,field} = this.props;    
    this.updateState = this.updateState.bind(this);
    this.clear = this.clear.bind(this);    
    const { initvalue} = this.props;
    if(initvalue){
      this.setState({text:initvalue});
      this.props.actions.UpdateForm(form,field,initvalue);
      this.validateData(initvalue);
    } 
  }

  componentWillReceiveProps(nextProps){
    const {form,field} = this.props;    
    var _initvalue = this.props.initvalue?this.props.initvalue:null;
    var next_initvalue = nextProps.initvalue?nextProps.initvalue:null;    
    if(_initvalue!==next_initvalue){
      this.setState({text:next_initvalue});
      this.props.actions.UpdateForm(form,field,next_initvalue);
      //this.validateData(next_initvalue);
    }
  }


  componentDidMount() {  
     
  }
  updateState(e){    
    this.setState({[e.key]:e.value});
  }

 
  handleKeyUp = e => {    
    if(e.keyCode){
      if(e.keyCode===13 || e.keyCode===27){    
        //
      } 
    }    
  };
  

OnChange(e){  
  var _th6 = this;
  const {props} = _th6;  
  const {form,field,email,address} = this.props;    
  if (typeof props.OnChange === 'function') { 
    props.OnChange(e);
  } 
}


updState(e){    
  this.setState({[e.key]:e.value});
}

handleFocus = e => {    
  this.setState({focus:true}); 
}

handleClickFocus = e => {
  var inF  = this.Elm.getElementsByTagName('input');   
  for(var i in inF){
    var ia = inF[i];            
    if(ia.childNodes){              
      if(this.Elm.contains(ia)){
        ia.focus();break;                
      }
    }
  } 
}

handleBlur = e => { 
  this.setState({focus:false}); 
  if (typeof this.props.OnBlur === 'function') { 
    this.props.OnBlur();    
  } 
}

clear = e => { 
  const { form } = this.props;  
  this.setState({error:false,text:''});
  this.props.actions.UpdateFormbyName(form,{});
}

handleChange(e){
  var v = e.target.value;  
  this.validateData(v);
};



validateData(v){
  const { form , email, ssn, address, maxValue, number, date, phone, field } = this.props;
  var valid = {v:true,m:''};
  if(email){
    Util.emailValidate(v)?valid={v:true,m:''}:valid={v:false,m:'email not valid'};
  }
  if(ssn){
    Util.ssnValidate(v)?valid={v:true,m:''}:valid={v:false,m:'ssn not valid'};
  }
  if(phone){
    Util.phoneValidate(v)?valid={v:true,m:''}:valid={v:false,m:'phone not valid'};
  }
  if(date){
    Util.dobValidate(v)?valid={v:true,m:''}:valid={v:false,m:'date not valid'};
  }
  if(number){
    if(isNaN(v)){
      valid={v:false,m:'number not valid'};
    }else{      
      if(maxValue){
        if(maxValue>=v){
          valid={v:true,m:''};
          v = parseFloat(v.toString());
        }else{
          valid={v:false,m:`value is greater than ${maxValue}`};
        }
      }else{
        valid={v:true,m:''};
        v = parseFloat(v.toString());
      }
    }    
  }  
  this.setState({text:v,valid:valid.v,msg:valid.m});   
  this.props.actions.UpdateForm(form,field,v);
  if (typeof this.props.OnChange === 'function') { 
    this.props.OnChange(v);       
  } 
};

ref = r => {
  this.Elm = r
}

render() {  
  var _th6 = this;
  const {number,email,placeholder,formObserve,dark,colorFloat,forms,form,field,password} = this.props;

  var labelFl = {position: 'absolute', transform: `translate3d(0px, 2px, 0)`}
  var BoxB = {}
  var inpSty = {}
  var ftext = forms[form]?forms[form][field]:this.state.text?this.state.text:null;    
  var labelField = ftext || '';  
  var isText = labelField?labelField.toString().length:this.state.text?this.state.text.toString().length:0;
  var _color_ = colorFloat?colorFloat:'var(--color-base--hover)';
  var customClass = '';

  if(isText>0){
    labelFl = {transform: `translate3d(-5px, -25px, 0)`,color:_color_}    
    customClass = '_valid';
  } 
  if(this.state.focus){
    customClass = '_focus';
    labelFl = {color:_color_, transform: `translate3d(-5px, -25px, 0)`};    
    BoxB = { borderColor: `${_color_}`,   
    //,padding: `15px 12px`
   }
  }
  if(!this.state.valid){    
    customClass = '_inValid';
    labelFl = {color:`rgb(255, 0, 0)`, transform: `translate3d(-5px, -25px, 0)`};
    BoxB = { borderColor: `#b71c1c`,
    //  ,padding: `15px 12px`
   }
  }
//<div className="INpyT2"  ></div>  
   
  var placeholderText = this.state.msg || placeholder;
  var inpuType = 'text';
  if(number){
      inpuType = 'number';
  }
  if(email){
      inpuType = 'email';
  }  
  if(password){
    inpuType = 'password';
  } 
  return(   
    <div className={`Wxwduf  ${customClass}`} style={BoxB} onClick={_th6.handleClickFocus.bind(_th6)} ref={this.ref} >      
            <input type={inpuType} value={labelField} onChange={_th6.handleChange.bind(_th6)} onFocus={_th6.handleFocus.bind(_th6)} onKeyUp={_th6.handleKeyUp.bind(_th6)}  onBlur={_th6.handleBlur.bind(_th6)} style={inpSty}/> 
            <div className="_label" style={labelFl} >{placeholderText}</div>
      
    </div>     
  )   
}
}



function mapStateToProps(state, ownProps) {
  return {
    forms: state.common.forms,
    formObserve: state.common.formObserve
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(commonActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(InputText);



