recalcdata(oo){
   const {filters,key,asc} = this.state;   
   if(Array.isArray(oo)){
      if(filters.sa){
         oo = oo.filter(d=>d['serviceAreaId'] === filters.sa)
      }
      if(filters.days){
         oo = oo.filter(d=>d['startTime']*1000 >= filters.days)
      }
   }
   
   var _key = sort_List[key];
   var _list_ = []
   if(Array.isArray(oo)){
      if(asc){
         _list_ = oo.sort(function(a, b) {
          if(a[_key] < b[_key]) { return -1; }
          if(a[_key] > b[_key]) { return 1; }
          return 0;          
        })
      }else{
         _list_ = oo.sort(function(a, b) {
          if(a[_key] > b[_key]) { return -1; }
          if(a[_key] < b[_key]) { return 1; }
          return 0;          
        })
      } 
   }     
  this.setState({_list:_list_});
}


  open_SortOptions(){     
   var formName = `${_Type}_${'sort'}`;    
   var _id = Util.Base64.encode(`_${formName}_`); 
   const {asc,key} = this.state;
   var _cont = <SortOptionFightDetails sortList={sort_List} _value={asc}  _active={key} updChange={this.handleOptionsSortChanges.bind(this)}/>
   var options = {id:_id,zIndex:150,height:420,content:_cont};
   this.props.dialogActions.OpenSlideOption(options);
}

close_SortOptions(){
   var formName = `${_Type}_${'sort'}`;   
   var _id = Util.Base64.encode(`_${formName}_`); 
   var options = {id:_id};
   this.props.dialogActions.CloseSlideOption(options);
}


  handleOptionsSortChanges(i){
   this.setState({asc:!this.state.asc,key:i})
   this.close_SortOptions();  
   this.recalcdata(this.props.fightDetails); 
 }
   


 open_FilterOptions(){    
   var formName = `${_Type}_${'filter'}`;    
   var _id = Util.Base64.encode(`_${formName}_`); 
   const {serviceAreas} = this.props;
   var _cont = <FilterOptionFightDetails serviceAreas={serviceAreas} filters={this.state.filters} updChange={this.handleOptionsFiltersChanges.bind(this)}/>   
   var options = {id:_id,zIndex:150,height:430,content:_cont};
   this.props.dialogActions.OpenSlideOption(options);
}

close_FilterOptions(){
   var formName = `${_Type}_${'filter'}`;   
   var _id = Util.Base64.encode(`_${formName}_`); 
   var options = {id:_id};
   this.props.dialogActions.CloseSlideOption(options);
}


handleOptionsFiltersChanges(i){
   this.close_FilterOptions();
   var _filter = this.state.filters;
   Util.ObjectKeys(i).map(fld=>{
      _filter[fld] = i[fld];
   })
   this.setState({filters:_filter});
   this.recalcdata(this.props.fightDetails);
 }




