import {genId} from '../state/Util';

class Home extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      route_list : [],
      setting:false,
      badUser:false
    };
  }



  componentWillMount(){  
    this.resize = this.resize.bind(this);
    window.addEventListener('resize',this.resize);    
    var _Id = genId();
    //console.log(_Id);
    this.props.commonActions.setSubId(_Id);
    //Subscription._EventSourceFunction(this.props.commonActions,_Id);
    this.props.commonActions.LoadData()
    var _th = this;
    let accessToken = false;
    //let idToken =  localStorage.getItem('_idToken_');
  
    if(accessToken){      
      let Credential2G = {
        accessToken:accessToken,
        //idToken:idToken
      }
      //var credential = commonActions._firebase.auth.GoogleAuthProvider.credential(Credential2G); 
      //commonActions._firebase.auth().signInWithCredential(credential)
    }
  }

  componentWillUnmount(){
    window.removeEventListener('resize',this.resize);
  }

  componentDidMount(){
    this.props.commonActions.LoadData();
  }


  



  resize(e){
    if(this.props.commonActions){
      this.props.commonActions.ScreenSize(window.outerWidth);
    }   
  }

 /*
 
  <Route path="/blocks" component={Blocks} /> 
                <Route path="/logs" component={Logs} /> 
 */

  render() {
    const {appLoaded,authenticate,_active,user} = this.props;
    if(appLoaded){
      if(authenticate){
        return (
          <div className={`App ${_active?'':'not_active'}`}>
             <Header/>          
            <div id="content_body">    
              <Switch>
                <Route exact path="/"  component={FightList} /> 
                <Route path="/createfight" component={user.email?CreateFight:null} />                  
                <Route path="/fightDetail" component={FightDetails} />   
                <Route path="/fightsList" component={FightList} />                                                     
              </Switch>
            </div>
            <DialogHRM/>
            <SlideOptionHRM/>
            <ViewHRM/>
          </div>
        );
      }else{
        return (<Login/>)
      }      
    }else{
      return (
        <div className="loadingScreen">
          <LoadingColorSpinner stroke={'#fff'}/>
        </div>
      )
    }
  }  
}


function mapStateToProps(state, ownProps) {
  return {       
    appLoaded: state.common.appLoaded,
    _active:state.common._active, 
    user:state.common.user, 
    authenticate: state.common.authenticate,

  };
}

function mapDispatchToProps(dispatch) {
  return {     
    commonActions: bindActionCreators(commonActions, dispatch)
  };
}


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Home));


